/**
 * todo DEVICE
 */
import {WinJSDec, fnError, appError} from './APP/config';

var name = 'DEVICE_INFO';
var DEVICE_INFO = (fn = _ => {
}) => {
  if (typeof fn === 'function') {
    WinJSDec(_ => {
      window.WinJSBridge.call('device', 'deviceInfo', function (res) {
        var {code, response} = res;
        if (code == 0) {
          fn(response);
        } else {
          throw new Error(`[${name}] ${appError}`);
        }
      });
    });
  } else {
    throw new TypeError(`[${name}] ${fnError}`);
  }
};
export default DEVICE_INFO;